import React from 'react';

import utils from '../../utils';

import './style.css';

let contentsMapping = {
  'LEGO': {
    background: '#fee452',
    height: 400,
    text: [utils.localize('lego.text1'), utils.localize('lego.text2')],
    src: require('../../sources/showcaseDetail/lego.png').default,
    banner: require('../../sources/showcaseDetail/lego_banner.jpg').default,
    logo: require('../../sources/showcaseDetail/lego_logo.png').default,
  },
  'POP MART': {
    background: '#fee452',
    height: 500,
    text: [utils.localize('pm.text1'), utils.localize('pm.text2')],
    src: require('../../sources/showcaseDetail/pm.png').default,
    banner: require('../../sources/showcaseDetail/pm_banner.png').default,
    logo: require('../../sources/showcaseDetail/pm_logo.png').default
  }
};

const lang = window.localStorage.getItem('leading_lang');
contentsMapping['Lioh Toy'] = lang === 'en' ? {
  imgs: [
    require('../../sources/showcaseDetail/lt_en1.png').default,
    require('../../sources/showcaseDetail/lt_en2.png').default,
    require('../../sources/showcaseDetail/lt_en3.png').default
  ]
} : {
  imgs: [
    require('../../sources/showcaseDetail/lt_zh1.png').default,
    require('../../sources/showcaseDetail/lt_zh2.png').default,
    require('../../sources/showcaseDetail/lt_zh3.png').default
  ]
};

class Toys extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const content = contentsMapping[this.props.title];
    if (this.props.title === 'Lioh Toy') {
      return (<div className="showcase-detail-content toys">
        {
          content.imgs.map((img, i) => {
            return <img key={i} className="showcase-detail-content-img" src={img} />
          })
        }
      </div>);
    }
    return (
      <div className="showcase-detail-content toys">
        <div className="showcase-detail-content-container">
          <div className="showcase-detail-content-background-container" style={{backgroundColor: content.background, height: content.height}}>
            <div className="showcase-detail-content-banner" style={{height: content.height - 50}}>
              <img src={content.banner} />
            </div>
            <div className="showcase-detail-content-text" style={{height: content.height - 50}}>
              <div className="showcase-detail-content-logo">
                <img src={content.logo} />
              </div>
              {
                content.text.map(t => {
                  return <p>{t}</p>
                })
              }
            </div>
          </div>
          <div className="showcase-detail-content-img-container">
            <img src ={content.src} />
          </div>
        </div>
      </div>
    );
  }
}

export default Toys;