import React from 'react';

import utils from '../../utils';

import './style.css';

const contentsMapping = {
  KFC: {
    src: require('../../sources/showcaseDetail/kfc.png').default,
    text: [utils.localize('kfc.text1'), utils.localize('kfc.text2')]
  }
};

class Catering extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const content = contentsMapping[this.props.title];
    return (
      <div className="showcase-detail-content catering">
        <div className="showcase-detail-content-background-container"></div>
        <div className="showcase-detail-content-container">
          <div className="showcase-detail-content-img-container">
            <img src ={content.src} />
            <div className="showcase-detail-content-text">
              {
                content.text.map(t => {
                  return <p>{t}</p>
                })
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Catering;
