import React from 'react';

import utils from '../../utils';

import './style.css';

const contentsMapping = {
  'BEAST': {
    background: '#0f4c7a',
    height: 1000,
    text: [utils.localize('beast.text1'), utils.localize('beast.text2'), utils.localize('beast.text3')],
    src: require('../../sources/showcaseDetail/beast.png').default,
    banner: require('../../sources/showcaseDetail/beast_banner.png').default,
    logo: require('../../sources/showcaseDetail/beast_logo.png').default,
  },
  'urbanista': {
    background: '#747474',
    height: 550,
    text: [utils.localize('urbanista.text1'), utils.localize('urbanista.text2')],
    src: require('../../sources/showcaseDetail/urbanista.png').default,
    banner: require('../../sources/showcaseDetail/urbanista_banner.png').default,
    logo: require('../../sources/showcaseDetail/urbanista_logo.png').default
  },
  'one61 Studio': {
    background: '#747474',
    height: 550,
    text: [utils.localize('os.text1'), utils.localize('os.text2')],
    src: require('../../sources/showcaseDetail/os.png').default,
    banner: require('../../sources/showcaseDetail/os_banner.png').default,
    logo: require('../../sources/showcaseDetail/os_logo.png').default
  }
};

class Household extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const content = contentsMapping[this.props.title];
    return (
      <div className="showcase-detail-content household">
        <div className="showcase-detail-content-container">
          <div className="showcase-detail-content-background-container" style={{backgroundColor: content.background, height: content.height}}>
            <div className="showcase-detail-content-banner" style={{height: content.height - 50}}>
              <img src={content.banner} />
            </div>
            <div className="showcase-detail-content-text" style={{height: content.height - 50}}>
              <div className="showcase-detail-content-logo">
                <img src={content.logo} />
              </div>
              {
                content.text.map(t => {
                  return <p>{t}</p>
                })
              }
            </div>
          </div>
          <div className="showcase-detail-content-img-container">
            <img src ={content.src} />
          </div>
        </div>
      </div>
    );
  }
}

export default Household;