class Fashion extends React.Component {
  constructor(props) {
    super(props);

    this.renderContent = this.renderContent.bind(this);
  }

  renderContent(content) {
    if (content) {
      switch (content.type) {
        case 'h':
          return (<div className="showcase-detail-h">{content.content}</div>);
        case 'p':
          return (<p>{content.content}</p>);
        case 'img':
          return (<div className="showcase-detail-img">
            <img src={content.src} />
          </div>);
      }
    }
    return null;
  }

  render() {
    return (
      <div className="showcase-detail-content fashion">
        <div className="showcase-detail-content-text-container">
          <div className="fashion-logo">
            <img src={logoMapping[this.props.title]} />
          </div>
        </div>
        <div className="showcase-detail-content-container">
          {
            contentsMapping[this.props.title].map(c => {
              return this.renderContent(c);
            })
          }
        </div>
      </div>
    );
  }
}

export default Fashion;
