import React from 'react';

import utils from '../../utils';

import './style.css';

const contentsMapping = {
  'PANDORA': {
    background: '#133e49',
    src: require('../../sources/showcaseDetail/pandora.png').default,
    text: [utils.localize('pandora.text1'), utils.localize('pandora.text2'), utils.localize('pandora.text3')]
  },
  'GUO JIN GOLD': {
    background: '#292929',
    src: require('../../sources/showcaseDetail/gjg.png').default,
    text: [utils.localize('gjg.text1'), utils.localize('gjg.text2')]
  }
};

class Accessories extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const content = contentsMapping[this.props.title];
    return (
      <div className="showcase-detail-content accessories">
        <div className="showcase-detail-content-container">
          <div className="showcase-detail-content-background-container" style={{backgroundColor: content.background}}></div>
          <div className="showcase-detail-content-img-container">
            <img src ={content.src} />
            <div className="showcase-detail-content-text">
              {
                content.text.map(t => {
                  return <p>{t}</p>
                })
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Accessories;