import React from 'react';

import utils from 'src/utils';
import Icon from 'src/components/icon';

import { withRouter } from 'react-router-dom';

import Fashion from './showcaseFashion';
import Accessories from './showcaseAccessories';
import Catering from './showcaseCatering';
import Household from './showcaseHousehold';
import Toys from './showcaseToys';

const typeItems = {
  fashion: ['SMFK', 'JOYRICH', 'PEACEBIRD', 'JUPE VENDUE', 'SPAO', 'Calvin Klein', 'Danielle Nicole', 'COACH', 'GAP', 'SHOE PALACE', 'CONVERSE', 'REEBOK'],
  accessories: ['PANDORA', 'GUO JIN GOLD'],
  catering: ['KFC'],
  household: ['BEAST', 'urbanista', 'one61 Studio'],
  toys: ['LEGO', 'POP MART', 'Lioh Toy']
};


class ShowcaseDetail extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedItem: typeItems[props.match.params.type][0]
    };

    this.jumpTo = this.jumpTo.bind(this);
    this.renderSidebar = this.renderSidebar.bind(this);
    this.renderContent = this.renderContent.bind(this);
  }

  jumpTo(link) {
    if (link === 'return') {
      this.props.history.push({pathname: 'showcase'});
    } else {
      this.setState({
        selectedItem: link
      });
    }
  }

  renderSidebar() {
    return (<div className="showcase-detail-menu">
      {
        [
          ...typeItems[this.props.match.params.type],
          'return'
        ].map(item => {
          return (<div className={`showcase-detail-menu-item ${item === this.state.selectedItem ? 'selected' : ''}`} onClick={this.jumpTo.bind(this, item)}>
            {
              item === 'return' ? (<div className="showcase-detail-menu-back">
                <Icon name="arrow-back" color="#fff" size={24} />
                {utils.localize('return')}
              </div>): item
            }
          </div>);
        })
      }
    </div>);
  }

  renderContent() {
    switch (this.props.match.params.type) {
      case 'fashion':
        return <Fashion title={this.state.selectedItem} />;
      case 'accessories':
        return <Accessories title={this.state.selectedItem} />;
      case 'catering':
        return <Catering title={this.state.selectedItem} />;
      case 'household':
        return <Household title={this.state.selectedItem} />;
      case 'toys':
        return <Toys title={this.state.selectedItem} />;
      default:
        return null;
    }
  }

  componentDidMount() {
    document.title = `${utils.localize('menuItem3')} - ${utils.localize('companyName')}`;
  }

  render() {
    const type = this.props.match.params.type;

    return (
      <div className="showcase-detail">
        {this.renderSidebar()}
        {this.renderContent()}
      </div>
    );
  }
}

export default withRouter(ShowcaseDetail);
